<template>
	<view class="">
		<!-- 搜索 -->
		<uni-search-bar
			@confirm="search"
			:focus="true"
			v-model="searchValue"
			placeholder="搜索"
			bgColor="#ccc"
		></uni-search-bar>
		<!-- 通告 -->
		<uni-notice-bar
			show-icon
			backgroundColor="#ccc"
			color="white"
			showClose
			scrollable
			showGetMore
			moreText="更多"
			@getmore="getMore"
			single
			text="梁婷婷是个大煞笔 "
			moreColor="white"
		></uni-notice-bar>

		<!--  -->

		<!-- 轮播 -->
		<swiper
			:indicator-dots="true"
			:autoplay="true"
			:interval="2000"
			:duration="1000"
			style="height: 400rpx;"
		>
			<swiper-item v-for="item in banner" :key="item.id">
				<view class="swiper-item">
					<image :src="item.pic" style="width: 750rpx; "></image>
				</view>
			</swiper-item>
		</swiper>

		<!-- 商品格 -->
	<uni-grid
				:column="2"
				:showBorder="false"
				:square="false"
				style="background-color: #f8f6f7;"
			>
				<uni-grid-item
					v-for="item in indexList"
					:key="item.id"
					style="background-color: white;border-radius: 10px;width:360rpx;margin-left: 8rpx;margin-top: 10rpx;overflow: hidden;"
				>
					<image :src="item.pic" style="width: 360rpx"></image>
					<view style="padding: 15rpx;margin-top: 10rpx;">
						<view style="font-size: 1.1em;">{{ item.name }}</view>
						<view style="font-size: 0.9em;color:gray">
							{{ item.desc }}
						</view>
						<view style="display: flex;justify-content: space-between;">
							<view
								style="font-size: 1.2em;color:red;font-weight: bold;"
							>
								¥{{ item.price }}
							</view>
							<view>{{ item.buyCount }}件</view>
						</view>
					</view>
				</uni-grid-item>
			</uni-grid>
	</view>
</template>

<script>
	import url from '../../config/url.js'
export default {
	
	data() {
		return {
			// 搜索
			searchValue: '',
			// 轮播
			banner: [
				{
					id: 1,
					pic:
						'https://wx4.sinaimg.cn/orj360/006d49NDgy1h83bzn9jrlj30v90v915l.jpg',
					co: '刘旭微也是'
				},
				{
					id: 2,
					pic:
						'https://wx3.sinaimg.cn/orj360/006d49NDgy1h83bzo0gqbj30v90v9471.jpg',
					co: '刘旭微也是'
				},
				{
					id: 3,
					pic:
						'https://wx2.sinaimg.cn/orj360/006d49NDgy1h83bzotq62j30v90v9wli.jpg'
					// co:'刘旭微也是''
				},
				{
					id: 4,
					pic:
						'https://wx4.sinaimg.cn/orj360/006d49NDgy1h83bzpdzc9j30v90v9459.jpg'
					// co:'刘旭微也是''
				},
				{
					id: 5,
					pic:
						'https://wx3.sinaimg.cn/orj360/006d49NDgy1h83bzm29p3j30v90v9tid.jpg'
					// co:'刘旭微也是''
				}
			],
		    
			// 商品
			indexList:[],
			// 商品页
			page: 1 ,
		};
	},




//   商品加载
	   onLoad(option) {
	   this.getIndexList()
	},
		onReachBottom() {
			// 获取后续页码的数据
			this.getIndexList(this.page)
		},

	methods: {
		// shang商品获取
		getIndexList(page=1){
			uni.request({
				url:url.indexList,
				data:{
					_page:page,
					_limit:4
					
				},
				success:(res)=>{
					console.log(res);
					// this.indexList = res.data
					// 新旧数据
					this.indexList = [...this.indexList, ...res.data]
					// yeshu
					
					this.page++
				},
			})
		},
		
		// 搜索
		search() {
			console.log('搜索：' + this.searchValue);
		},

		getMore() {
			uni.showToast({
				title: '你想得美'
			});
		}

		
		
		
		
		
		
	}
};
</script>

<style lang="scss">
uni-swiper-item {
	border-radius: 20px;
}
</style>
